* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}.

main {
  font-family: "Heebo", sans-serif;
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 10vh;
  color: white;
}

section {
  height: 100vh;
}

li {
  background-image: none !important;
}

.gs_reveal {
  opacity: 0;
  visibility: hidden;
  will-change: transform, opacity;
}

.nav-links {
  display: flex;
  list-style: none;
}

.nav-items {
  color: white;
  text-decoration: none;
}

.nav-items:hover {
  color: white;
  text-decoration: none;
}

.nav-item-animate {
  transition: transform 1s;
}

.nav-item-animate:hover {
  transform: scale(1.1);
}

.nav-links li {
  padding-left: 10rem;
  font-size: 1.2rem;
}

#logo {
  font-weight: lighter;
  font-size: 2rem;
}

.landing {
  height: 100vh;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: 100% 100%;
  padding: 0rem 5rem;
}

.title-text {
  position: absolute;
  top: 35%;
  left: 50%;
  transform: translate(-50%, -30%);
  font-size: 6rem;
  font-weight: normal;
  color: white;
}

.intro {
  background: black;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider {
  background: rgb(97, 105, 109);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: translateY(100%);
}

.intro-text {
  color: rgb(233, 233, 233);
  font-family: "Heebo", sans-serif;
  font-size: 3rem;
}

.hide {
  background: black;
  overflow: hidden;
}

.hide span {
  transform: translateY(100%);
  display: inline-block;
}

.about {
  height: 100vh;
}

.about-title {
  position: relative;
  top: 20%;
  left: 15%;
  font-size: 50px;
  width: 50%;
  opacity: 0;
}

.about-paragraph {
  position: relative;
  top: 25%;
  left: 15%;
  padding: 3%;
  padding-right: 10%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 20px;
  width: 45vw;
  height: 40vh;
}

.profile-pic {
  position: absolute;
  top: 110%;
  left: 60%;
  width: 20%;
  z-index: 1;
  /* transition: transform 0.5s; */
  border-radius: 15px;
  opacity: 0;
}

.timeline {
  width: 60%;
  height: 100px;
  margin: auto;
  margin-top: 20px;
  font-size: 14px;
  top: 20%;
  opacity: 0;
  position: relative;
}

.experience-card {
  position: absolute;
  margin: auto;
  padding: 3%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 20px;
  width: 45vw;
  height: 40vh;
  top: 245%;
  left: 0;
  right: 0;
  opacity: 0;
  margin-left: auto;
  margin-right: auto;
}

.header {
  text-align: center;
  position: relative;
  opacity: 0;
  top: -5%;
  font-size: 50px;
}

.round {
  border-radius: 50%;
  width: 100px;
  transition: transform 0.5s;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.round:hover {
  transform: scale(1.1);
}

.selected {
  background-color: #f85084 !important;
  color: white !important;
}

#projects {
  min-height: 100vh;
}

.projectContainer {
  position: relative;
  margin-top: 13%;
  opacity: 0;
}

.buttonList {
  padding: 0 20%;
}

.projectList {
  margin-top: 5%;
}

.projectCol {
  padding: 20px;
}

.projectCard {
  padding: 2%;
}

