.contacts-list {
  height: 20vh;
  width: 45vw;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  top: 15rem;
  opacity: 0;
}

.bye-gif {
  position: relative;
  opacity: 0.2;
  top: 10rem;
  left: -200px;
  text-align: center;
}

.fa-facebook {
  background: #3B5998;
  color: white;
}

.linkedin {
  width: 62px;
  margin-top: -15px;
}

.mail {
  width: 58px;
  margin-top: -15px;
}

.icons {
  font-size: 4rem;
}

.contact-icon {
  color: black;
  transition: transform 0.5s;
}

.contact-icon:hover {
  color: black;
  transform: scale(1.2);
}

