@media (max-width:641px) {
  .title-text {
    font-size: 3rem;
    width: 15rem;
    text-align: center;
  }

  .landing {
    height: 75vh;
    background-size: 100% 75%;
  }

  .nav-bar {
    display: none;
  }

  .about-title {
    left: 0%;
    font-size: 35px;
  }

  .about-paragraph {
    left: -14%;
    height: 45vh;
    width: 75vw;
  }

  .profile-pic {
    left: 25%;
    width: 50%;
    top: 133%;
  }

  #experiences {
    margin-top: 0rem;
  }

  .header {
    font-size: 35px;
    top: 10%;
  }

  .timeline {
    top: 35%;
    width: 85%;
  }

  .experience-card {
    top: 235%;
    width: 85vw;
  }

  .experience-desc {
    min-height: 33vh;
  }

  .company-image {
    text-align: center;
  }

  .round {
    width: 100px;
  }

  .projectContainer {
    margin-top: 50%;
  }

  .contacts-list {
    left: -12px;
  }

  .icons {
    font-size: 3rem;
  }

  .linkedin {
    width: 46px;
    margin-top: -12px;
  }

  .mail {
    width: 43px;
    margin-top: -11px;
    margin-left: 5px;
  }

  .gif {
    width: 15rem;
  }
}

